import React from 'react'
import { connect } from 'react-redux'
import { Input, Button, Icon, Modal } from 'antd'
import { listDept } from '/api/mtn'
import Common from '/page/common.jsx'


class Department extends Common{
	async componentWillMount () {
		this.search()
	}

	state = {
		columns: [{
			title: '部门名称',
			dataIndex: 'name'
		}, {
			title: '部门代码',
			dataIndex: 'code'
		}, {
			title: '修改时间',
			dataIndex: 'updateTime'
		}, {
			title: '是否启用',
			dataIndex: 'enabled',
			render: t => t ? '是' : '否'
		}],
		selected: {},
		loading: true,
		tabledata: [],
		modalconf: {visible: false, item: {}},
		search: {},
	}

	search = async _ => {
		await this.setState({loading: true, selected: {}})
		let search = Object.assign({}, this.state.search)
		if (!search.condition) delete search.condition
		return listDept(search)
		.then(res => {
			let data

			data = (f => f(f))(f => list => list.map(val => {
				let baseItem = Object.assign({}, val, { key: val.id })
				if (val.childrenDepts && val.childrenDepts.length) {
					let item = Object.assign({}, baseItem, {
						children: f(f)(val.childrenDepts)
					})
					delete item.childrenDepts
					return item
				}
				return baseItem
			}))(res.data)

			this.setState({
				tabledata: data, 
				loading: false
			})
		})
	}


	rendermodal = _ => []

}

export default Department


